import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  TextField,
  makeStyles,
  Tooltip
} from '@material-ui/core';
import wait from 'src/utils/wait';
import {addGroup} from '../../../api/groups.js';
import { useHistory } from 'react-router-dom';
import useAuth from 'src/hooks/useAuth';
import Hashids from 'hashids'
const useStyles = makeStyles(() => ({
  root: {}
}));

const GroupCreateForm = ({
  className,
  ...rest
}) => {

  const classes = useStyles();
  const history = useHistory();
  const { enqueueSnackbar } = useSnackbar();
  const { user } = useAuth();
  const hashids = new Hashids();
    return (
    <Formik
    initialValues={{
      name: '',
      hash_id:hashids.encode(parseInt(new Date().getTime()/ 1000)),
      submit: null
    }}
      validationSchema={Yup.object().shape({
        name: Yup.string().max(255).required('Name is required'),
        hash_id: Yup.string().max(15),
      })}
      onSubmit={async (values, {
        resetForm,
        setErrors,
        setStatus,
        setSubmitting
      }) => {
        try {
          values.name = values.name.replace(/\s/g, '');
          values.created_by =  user.userId;
          values.created_at =  new Date();
          const result = await addGroup(values);
          console.log(result);
          // NOTE: Make API request
          await wait(500);
          if(result == 'success'){
            enqueueSnackbar('Group create', {
              variant: 'success',
              action: <Button>See all</Button>
            });
            history.push('/teacher/management/groups');
          }else{
            enqueueSnackbar('Group create', {
              variant: 'error',
              action: <Button>See all</Button>
            });
          }
        } catch (err) {
          console.error(err);
          setStatus({ success: false });
          setErrors({ submit: err.message });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        touched,
        values
      }) => (
        <form className={clsx(classes.root, className)} onSubmit={handleSubmit}{...rest}>
          <Card>
            <CardContent>
              <Grid container spacing={3}>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.name && errors.name)}
                    fullWidth
                    helperText={touched.name && errors.name}
                    label="Tên Nhóm"
                    name="name"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.name}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.hash_id && errors.hash_id)}
                    fullWidth
                    helperText={touched.hash_id && errors.hash_id}
                    label="Mã nhóm"
                    name="hash_id"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.hash_id}
                    variant="outlined"
                    disabled
                  />
                </Grid>
                <Grid item />

              </Grid>
              <Box mt={2}>
                <Button
                  variant="contained"
                  color="secondary"
                  type="submit"
                  disabled={isSubmitting}
                >
                  Lưu
                </Button>
              </Box>
            </CardContent>
          </Card>
        </form>
      )}
    </Formik>
  );
};

GroupCreateForm.propTypes = {
  className: PropTypes.string
};

export default GroupCreateForm;
